.CVBuilder {
    position: relative;
    max-width: 90rem;
    margin: 0 auto;
    background-color: #ffffff;
    padding: 3rem;
    padding-bottom: 2rem;
    border: 1px solid #b3b3b3;
    background-color: rgba(255, 255, 255, 0.99);
}

.input {
    position: absolute;
    top: -10%;
    left: 0;
    width: 100%;
    height: inherit;
    color: inherit;
    font: inherit;
    border: 1px solid #f5ae45;
    border-radius: 10px;
    outline: none;
    display: inherit;
    padding: inherit;
    font-size: inherit;
    margin: inherit;
}

.input::-webkit-input-placeholder {
    color: inherit;
    font-weight: 300;
    text-align: center;
}

.input::-moz-placeholder {
    color: inherit;
    font-weight: 300;
    text-align: center;
}

.input::-ms-input-placeholder {
    color: inherit;
    font-weight: 300;
    text-align: center;
}

.input::placeholder {
    color: inherit;
    font-weight: 300;
    text-align: center;
}

.textChange {
    text-transform: capitalize;
}

