.Edit {
    position: fixed;
    top: 12rem;
    right: 30rem;
    width: 15rem;
    height: auto;
    padding: 0 1rem;
    z-index: 1000;
}

.Edit_Btn {
    display: inline-block;
    cursor: pointer;
    color: #b3b3b3;
    -webkit-transition: all .2s;
    transition: all .2s;
}

.Edit_Btn:hover {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    color: #f5af45d3;
}

.Edit_Btn:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    color: #f5af45d3;
}

.Edit_Add {
    margin-top: 3rem;
    font-size: 1.6rem;
    border-radius: 1rem;
    overflow: hidden;
    text-align: center;
    cursor: pointer;
}

.Edit_Add p {
    border-radius: 1rem;
    padding: 1rem 0;
    margin-bottom: 1rem;
    background-color: #f5af45d3;
    font-weight: 700;
    -webkit-transition: all .2s;
    transition: all .2s;
}
p.Edit_Default {
    background-color: #88ffba;
}

.Edit_Add p:hover {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 0 5px 8px #8f8f8f;
}
.Edit_Add p:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    box-shadow: 0 3px 5px #555555;
}

@media (max-width: 1800px) {
    .Edit {
        right: 10rem;
    }
}

@media (max-width: 1500px) {
    .Edit {
        right: 10rem;
    }
}

@media (max-width: 1400px) {
    .Edit {
        right: 2rem;
    }
}

@media (max-width: 750px) {
    .Edit {
        right: 5rem;
        width: 12rem;
    }
}

@media (max-width: 500px) {
    .Edit {
        right: 1rem;
    }
}

@media (max-width: 450px) {
    .Edit {
        top: 15rem;
        padding: 1rem .8rem;
        border-radius: 1rem;
        right: 0rem;
        background-color: rgb(255, 234, 178);
    }
    .Edit_Add {
        margin-top: 1rem;
    }
}
.Top {
    display: flex;
    margin-bottom: 1rem;
}
.ImgUpload {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    align-self: stretch; 
}

.ImgUpload-file {
    display: none;
}

.ImgUpload-img{
    width: 100%;
    object-fit: cover;
    border: 3px double #f5ae45;
    border-radius: 10px;
    box-shadow: 0 1rem 1.5rem rgb(201, 200, 200);
    -webkit-filter: grayscale(10%) brightness(110%);
            filter: grayscale(10%) brightness(110%);
    position: relative;
}

.Img_control {
    display: flex;
    font-size: 1.6rem;
    justify-content: center;
    padding: 10px 0;
}
progress {
    width: 90%;
    margin: 12px auto 0 auto;
}

.Img_control p {
    cursor: pointer;
    padding: 5px 10px;
    background-color: darkcyan;
    color: #f8e3c4;
    letter-spacing: 1px;
    font-weight: 700;
    border-radius: 5px;
    -webkit-transition: all .2s;
    transition: all .2s;
}
.Img_control p:hover {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
}
.Img_control p:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}
.Info {
    flex: 0 1 75%;
