}
.Info-inlineList {
    list-style-type: disc;
    font-size: 1.9rem;
    border-top: 2px solid #f5ae45;
    /* border-bottom: 2px solid #f5ae45; */
    margin-top: 2rem;
    padding: 1rem 0;
}

.Info-inlineList li {
    margin: 5px auto;
    width: 85%;
    display: flex;
    justify-content: space-between;
}

@media only screen and (max-width: 600px) {
    .Info {
        flex: 0 1 65%;
        padding-left: 2rem;
    }
    .Info-fullName {
        font-size: 4rem;
    }
