.Body_control {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.Body_subtitle {
    padding: 5px 10px;
    border-radius: 5px;
    background-color: #e7eaff;
    margin-right: 2px;
    -webkit-transition: all .2s;
    transition: all .2s;
}
.Wrapper_delete {
    font-size: 2.5rem;
    padding: 10px;
    color: #ff4f4f;
    -webkit-transition: all .2s;
    transition: all .2s;
}
.Wrapper_delete:hover,
.Body_subtitle:hover {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
}
.Wrapper_delete:active,
.Body_subtitle:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}

.AddItem {
    margin-left: 5px;
