.Auth {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    font-size: 3rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.Auth .card {
    position: relative;
    background-color: rgb(202, 186, 162);
    width: 50rem;
    padding: 5rem 3rem;
}

.Auth img {
    position: absolute;
    width: 10rem;
    height: 10rem;
    object-fit: cover;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 50%;
}

.Auth h3 {
    font-size: 4.6rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 3rem;
}

